import React from "react";

import "./FhaLoanRates.css";

function FhaLoanRates() {
  return (
    <div
      id="fha-loan-rates"
      className="fha-loan-rates dark-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-5.png"
              alt="/images/loan-guide/s-5.png"
            />
            <p className="header-text-3">SECTION 5</p>
            <h4 className="header-text-1">FHA Loan Rates</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              While this is a government-backed loan, there is not a set
              interest rate for FHA loans. FHA lenders set and quote their own
              interest rates. Rates can vary depending on the lender, the loan
              type, the borrower’s credit score and more.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1">
              Fixed interest rates on FHA loans tend to be slightly lower than
              those on conventional mortgage programs, simply because lenders
              have more protection. They have the added insurance of FHA’s
              backing, which allows them to take extra risk and offer lower
              rates.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <a href="/mortgage-rates" className="sub-text-1">
              <b>MORE:</b> Check Out Today's FHA loan rates
            </a>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanRates;
