import React from "react";

import "./FhaLoanTypes.css";

function FhaLoanTypes() {
  return (
    <div
      id="fha-loan-types"
      className="fha-loan-types light-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-2.png"
              alt="/images/loan-guide/s-2.png"
            />
            <p className="header-text-3">SECTION 2</p>
            <h4 className="header-text-1">FHA Loan Types</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              There are more than a dozen FHA mortgage programs, with each
              varying slightly in structure and use. The most common FHA loan
              types are:
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Fixed-Rate FHA Loan</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              Fixed-rate mortgages are the most common type of FHA loan, as they
              offer a reliable, consistent payment that homeowners can count on.
              These typically come in 15- and 30-year terms and have the same
              interest rate for the entirety of that term. While the government
              fully backs the loan, lenders set their own interest rates.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Adjustable Rate Mortgage (ARM)</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              Adjustable-rate FHA loans have interest rates that vary over time.
              They may have a period of 3, 5 or 7 years in which the initial low
              rate is fixed, but after that period lapses, the rate can rise.
              This means your mortgage payment would rise as well.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">FHA Energy Efficient Mortgage (EEM)</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              FHA Energy Efficient Mortgages, or EEMs, encourage homeowners to
              make energy-efficient upgrades on their properties. The loans can
              be used to cover the costs of acceptable energy-related
              improvements on an existing home or a new home you’re purchasing.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Other FHA Loan Types</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              There are other, less-used FHA loan types as well, including the
              203(k) – a mortgage generally reserved for fixed-uppers. The FHA
              203(k) loan allows you to roll the costs of your repair and
              renovation expenses into your mortgage, which is based on the
              expected higher value of the property after those improvements are
              made.
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              There are also streamline refinance FHA loans, which offer
              existing FHA borrowers a quick and easy refinancing process, as
              well as Title 1 FHA loans that cover home improvements and
              repairs.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanTypes;
