import React from "react";

import QuoteSteps from "../../../QuoteSteps/QuoteSteps";

import "./ApplyFhaLoan.css";

function ApplyFhaLoan() {
  return (
    <div
      id="apply-fha-loan"
      className="apply-fha-loan light-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <p className="header-text-3">SECTION 10</p>
            <h4 className="header-text-1">How to Apply for an FHA Loan</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              The first step is to find a lender approved by the FHA. You can
              use{" "}
              <a href="https://www.hud.gov/program_offices/housing/sfh/lender/lenderlist">
                HUD’s Lender List Search tool
              </a>{" "}
              to find one in your area.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              Then, you’ll need to:
            </p>
            <ul>
              <li>
                <p className="sub-text-1">
                  Talk with a loan specialist about getting preapproved. This
                  will tell you if you meet the minimum eligibility requirements
                  for an FHA loan and the maximum loan you can apply for.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  Find your property and sign a purchase agreement with the
                  seller.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  Finalize your loan application with your lender.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  Await your appraisal. The lender will order an appraisal of
                  the property to ensure it meets FHA standards.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  Wait as your loan is underwritten and processed by the lender.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  Attend closing, This is when you’ll sign your papers, pay your
                  closing costs and get your keys.
                </p>
              </li>
              <li>
                <p className="sub-text-1">Move into your new home!</p>
              </li>
            </ul>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              Make sure you stay in touch with your loan officer throughout the
              process, as they may need additional documentation along the way.
              The more responsive you are, the better. Any missing paperwork
              would delay your closing and your move onto the property.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-12 col-md-12 col-lg-12 mt-5">
            <hr className="loan-apply-hr"></hr>
            <QuoteSteps stepOneSub="See if you qualify for a Low Money Down FHA Home Loan" />
            <hr className="loan-apply-hr"></hr>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ApplyFhaLoan;
