import React from "react";

import NavBar from "../../../components/NavBar/NavBar";
import StepCard from "../../../components/StepCard/StepCard";

import "./FhaLoanGuide.css";
import WhatFhaLoan from "./WhatFhaLoan/WhatFhaLoan";
import FhaLoanTypes from "./FhaLoanTypes/FhaLoanTypes";
import FhaLoanEligibility from "./FhaLoanEligibility/FhaLoanEligibility";
import FhaLoanBenefits from "./FhaLoanBenefits/FhaLoanBenefits";
import FhaLoanRates from "./FhaLoanRates/FhaLoanRates";
import FhaLoanInsurance from "./FhaLoanInsurance/FhaLoanInsurance";
import FhaLoanLimits from "./FhaLoanLimits/FhaLoanLimits";
import FhaLoanClosing from "./FhaLoanClosing/FhaLoanClosing";
import FhaLoanRefinancing from "./FhaLoanRefinancing/FhaLoanRefinancing";
import ApplyFhaLoan from "./ApplyFhaLoan/ApplyFhaLoan";

function FhaLoanGuide() {
  return (
    <div>
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7 center">
            <h4 className="header-text-1">FHA Loan Program</h4>
            <h3 className="header-text-2">Complete Guide to the FHA Loan</h3>
            <p className="header-text-3">
              In this comprehensive guide to Federal Housing Administration
              loans, or FHA loans, we will cover:
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5">
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#what-fha-loan",
                topTxt: "Section 1",
                subTxt: "What is an FHA Loan?",
                imgSrc: "/images/loan-guide/s-1.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-types",
                topTxt: "Section 2",
                subTxt: "FHA Loan Types",
                imgSrc: "/images/loan-guide/s-2.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-eligibility",
                topTxt: "Section 3",
                subTxt: "FHA Loan Eligibility",
                imgSrc: "/images/loan-guide/s-3.png",
              }}
            />
          </div>
        </div>
        <div className="row d-flex justify-content-center">
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-benefits",
                topTxt: "Section 4",
                subTxt: "FHA Loan Benefits",
                imgSrc: "/images/loan-guide/s-4.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-rates",
                topTxt: "Section 5",
                subTxt: "FHA Loan Rates",
                imgSrc: "/images/loan-guide/s-5.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-mortgage-insurance",
                topTxt: "Section 6",
                subTxt: "FHA Loans and Mortgage Insurance",
                imgSrc: "/images/loan-guide/s-6.png",
              }}
            />
          </div>
        </div>
        <div className="row d-flex justify-content-center">
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-limit",
                topTxt: "Section 7",
                subTxt: "FHA Loan Limits",
                imgSrc: "/images/loan-guide/s-7.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-closing-cost",
                topTxt: "Section 8",
                subTxt: "FHA Loan Closing Costs",
                imgSrc: "/images/loan-guide/s-8.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fha-loan-refinancing",
                topTxt: "Section 9",
                subTxt: "FHA Loan Refinancing",
                imgSrc: "/images/loan-guide/s-9.png",
              }}
            />
          </div>
        </div>
        <div className="row d-flex justify-content-center">
          <div className="col-sm-9 center mb-5">
            <a href="#apply-fha-loan" className="step-card">
              <div className="row">
                <div className="col-9 col-sm-12">
                  <h5>Section 10</h5>
                  <p>How to Apply for an FHA Loan</p>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
      <WhatFhaLoan />
      <FhaLoanTypes />
      <FhaLoanEligibility />
      <FhaLoanBenefits />
      <FhaLoanRates />
      <FhaLoanInsurance />
      <FhaLoanLimits />
      <FhaLoanClosing />
      <FhaLoanRefinancing />
      <ApplyFhaLoan />
    </div>
  );
}

export default FhaLoanGuide;
