import "./StepCard.css";

function StepCard({ stepData }) {
  return (
    <a href={stepData.link} className="step-card">
      <div className="row">
        <div className="col-3 col-sm-12">
          <img src={stepData.imgSrc} alt={stepData.imgSrc} />
        </div>
        <div className="col-9 col-sm-12">
          <h5>{stepData.topTxt}</h5>
          <p>{stepData.subTxt}</p>
        </div>
      </div>
    </a>
  );
}

export default StepCard;
