import React from "react";

import "./FhaLoanRefinancing.css";

function FhaLoanRefinancing() {
  return (
    <div
      id="fha-loan-refinancing"
      className="fha-loan-refinancing dark-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-9.png"
              alt="/images/loan-guide/s-9.png"
            />
            <p className="header-text-3">SECTION 9</p>
            <h4 className="header-text-1">FHA Loan Refinancing</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              If you already have a home loan, you can opt to use an FHA
              mortgage when refinancing your existing loan. You might do this if
              mortgage rates fall below your current loan’s rate or if the fixed
              period of your adjustable-rate loan is ending.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">FHA Streamline Refinance</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              If you have an FHA loan and want to refinance to a new one, it's
              called a streamline refinance. To qualify, you need to have made
              at least six payments on your current loan and your closing date
              must be at least 210 days past.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Conventional to FHA</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              If you have a conventional loan, refinancing to an FHA mortgage
              can have big benefits. You can refinance up to 96.5 percent of
              your current home value using an FHA refinance.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-5 center">
            <div className="card-start-tab-dark">
              <p className="sub-text-1">Start My FHA Loan Refinance</p>
              <a className="link" href="/start">
                Find Out How Much an FHA Refinance Can Save You! →
              </a>
            </div>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Net Tangible Benefit</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              In order to refinance an existing FHA loan, you need to see “net
              tangible benefit” from the transaction. This simply means that the
              refinance improves your financial position in one of the following
              ways:
            </p>
            <ul>
              <li>
                <p className="sub-text-1">
                  The principal loan balance and interest is reduced by at least
                  5 percent.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  The new adjustable rate is 2 percent lower than your current
                  fixed rate.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  The new fixed rate is less than 2 percent higher than your
                  current adjustable rate.
                </p>
              </li>
              <li>
                <p className="sub-text-1">
                  The term of the loan has been reduced.
                </p>
              </li>
            </ul>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Cash-Out Refinance</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              If you've owned your home for a while and need to pay for home
              repairs, college tuition, debts or just need extra funds, then a
              cash-out FHA refinance offers a solution. Using these loans,
              qualified buyers can look to refinance up to 85 percent of the
              home's value.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanRefinancing;
