import React from "react";
import DataTable from "react-data-table-component";

import "./FhaLoanInsurance.css";

function FhaLoanInsurance() {
  const columnsAnnualMIP = [
    {
      name: "Loan Term",
      selector: (row) => row.term,
    },
    {
      name: "Loan Amount",
      selector: (row) => row.amount,
    },
    {
      name: "LTV Ratio",
      selector: (row) => row.ratio,
    },
    {
      name: "Annual Insurance Premium",
      selector: (row) => row.premium,
    },
  ];

  const dataAnnualMIP = [
    {
      id: 1,
      term: "Over 15 years",
      amount: "$625,000 or less",
      ratio: "95 percent or less",
      premium: "0.80 percent",
    },
    {
      id: 2,
      term: "Over 15 years",
      amount: "$625,000 or less",
      ratio: "Over 95 percent",
      premium: "0.85 percent",
    },
    {
      id: 3,
      term: "Over 15 years",
      amount: "Over $625,000",
      ratio: "95 percent or less",
      premium: "1 percent",
    },
    {
      id: 4,
      term: "Over 15 years",
      amount: "Over $625,000",
      ratio: "Over 95 percent",
      premium: "1.05 percent",
    },
    {
      id: 5,
      term: "15 years or less",
      amount: "$625,000 or less",
      ratio: "95 percent or less",
      premium: "0.45 percent",
    },
    {
      id: 6,
      term: "15 years or less",
      amount: "$625,000 or less",
      ratio: "Over 95 percent",
      premium: "0.70 percent",
    },
    {
      id: 7,
      term: "15 years or less",
      amount: "Over $625,000",
      ratio: "95 percent or less",
      premium: "0.70 percent",
    },
    {
      id: 8,
      term: "15 years or less",
      amount: "Over $625,000",
      ratio: "Over 95 percent",
      premium: "0.95 percent",
    },
  ];

  const columnsAnnualMIPPaying1 = [
    {
      name: "Term",
      selector: (row) => row.term,
    },
    {
      name: "Original Down Payment",
      selector: (row) => row.downPayment,
    },
    {
      name: "Duration",
      selector: (row) => row.duration,
    },
  ];

  const dataAnnualMIPPaying1 = [
    {
      id: 1,
      term: "15 years or less",
      downPayment: "22 percent or higher",
      duration: "No annual MIP",
    },
    {
      id: 2,
      term: "15 years or less",
      downPayment: "less than 22 percent",
      duration: "Cancelled at 78 percent LTV",
    },
    {
      id: 3,
      term: "Over 15 years",
      downPayment: "22 percent or higher",
      duration: "5 years",
    },
    {
      id: 4,
      term: "Over 15 years",
      downPayment: "less than 22 percent",
      duration: "Cancelled at 78 percent LTV (5 years minimum)",
    },
  ];

  const columnsAnnualMIPPaying2 = [
    {
      name: "Loan Term",
      selector: (row) => row.term,
    },
    {
      name: "Original Down Payment",
      selector: (row) => row.downPayment,
    },
    {
      name: "Duration",
      selector: (row) => row.duration,
    },
  ];

  const dataAnnualMIPPaying2 = [
    {
      id: 1,
      term: "15 years or less",
      downPayment: "less than 10 percent",
      duration: "Life of loan",
    },
    {
      id: 2,
      term: "15 years or less",
      downPayment: "10 percent or higher",
      duration: "11 years",
    },
    {
      id: 3,
      term: "Over 15 years",
      downPayment: "less than 10 percent",
      duration: "Life of loan",
    },
    {
      id: 4,
      term: "Over 15 years",
      downPayment: "10 percent or higher",
      duration: "11 years",
    },
  ];

  return (
    <div
      id="fha-loan-mortgage-insurance"
      className="fha-loan-mortgage-insurance light-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-6.png"
              alt="/images/loan-guide/s-6.png"
            />
            <p className="header-text-3">SECTION 6</p>
            <h4 className="header-text-1">FHA Loans and Mortgage Insurance</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              In order to allow for such low down payments and credit scores,
              the FHA does require you pay a Mortgage Insurance Premium (MIP) to
              protect the agency in case you default. You’ll pay this once as an
              upfront fee at closing and then again month after month, along
              with your mortgage payment.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Upfront MIP</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              This is a one-time MIP payment you’ll make at closing. It costs
              1.75 percent of your total home loan balance (not the home’s
              price), regardless of your credit score or debt-to-income ratio.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Annual MIP</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              Your annual MIP payments will be calculated every year and built
              into your monthly mortgage payments. The total of these payments
              will depend on your loan-to-value ratio, your loan balance and
              your loan’s term.
            </p>
            <DataTable
              className="guide-d-table"
              columns={columnsAnnualMIP}
              data={dataAnnualMIP}
            />
            <p className="sub-text-1 sub-text-margin-bottom mt-5">
              Here’s an example: If you had a 30-year loan of $200,000 and an
              loan-to-value ratio of 90 percent, your annual MIP would be
              $1,600. Spread out over 12 months, that would mean about $133 paid
              in MIP every month.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Paying the Annual MIP</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              You may not have to pay your annual MIP forever. Depending on your
              loan term, your loan-to-value ratio and when your loan was
              originated, you may be able to cancel your MIP and subsequently
              lower your monthly payment.
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              For loans originated before June 3, 2013, MIP durations are as
              follows:
            </p>
            <DataTable
              columns={columnsAnnualMIPPaying1}
              data={dataAnnualMIPPaying1}
            />
            <p className="sub-text-1 sub-text-margin-bottom mt-5">
              On FHA loans originated after June 3, 2013, annual MIP durations
              look like this:
            </p>
            <DataTable
              columns={columnsAnnualMIPPaying2}
              data={dataAnnualMIPPaying2}
            />
          </div>
        </div>

        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanInsurance;
