import React from "react";

import "./WhatFhaLoan.css";

function WhatFhaLoan() {
  return (
    <div
      id="what-fha-loan"
      className="what-fha-loan dark-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-1.png"
              alt="/images/loan-guide/s-1.png"
            />
            <p className="header-text-3">SECTION 1</p>
            <h4 className="header-text-1">What is an FHA Loan?</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              An FHA loan is a mortgage loan option for U.S. home buyers. It is
              guaranteed by the Federal Housing Administration, an agency of the
              Department of Housing and Urban Development, and issued by
              FHA-approved lenders across the country.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1">
              FHA loans are well known for their affordability – namely their
              3.5% percent down payment and relatively lenient credit
              requirements. According to HUD, the FHA has insured more than 47
              million mortgage loans since its inception in 1934.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default WhatFhaLoan;
