import React from "react";

import "./FhaLoanBenefits.css";

function FhaLoanBenefits() {
  return (
    <div
      id="fha-loan-benefits"
      className="fha-loan-benefits light-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-4.png"
              alt="/images/loan-guide/s-4.png"
            />
            <p className="header-text-3">SECTION 4</p>
            <h4 className="header-text-1">FHA Loan Benefits</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-2"></div>
          <div className="col-sm-6">
            <p className="sub-text-1 sub-text-margin-bottom">
              Thanks to their relaxed credit and debt-to-income requirements,
              FHA loans are largely considered one of the easiest mortgage
              products to qualify for. They also require minimal down payments,
              making them ideal for borrowers without a lot of savings.
            </p>
          </div>
          <div className="col-sm-1"></div>
          <div className="col-sm-3 center">
            <a className="start-set-div" href="/start">
              <hr className="loan-details-hr m-auto" />
              <p className="header-text-3 mt-3">FIRST TIME HOMEBUYERS:</p>
              <p className="sub-text-1 sub-text-margin-bottom">
                "The FHA loan is particularly popular with first-time home
                buyers, largely because of its low down payment requirements."
              </p>
              <p>See if you're eligible today!</p>
              <hr className="loan-details-hr m-auto" />
            </a>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Relaxed Credit Score Requirements</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              FHA loans come with low credit score requirements when compared to
              other loan options, requiring a minimum of just 500. Typically,
              conventional loans requires a score of 620, while USDA loans
              require a 600 or higher.
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              Credit score benchmarks for FHA loans will vary lender.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Adjustable Rate Mortgage (ARM)</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              Adjustable-rate FHA loans have interest rates that vary over time.
              They may have a period of 3, 5 or 7 years in which the initial low
              rate is fixed, but after that period lapses, the rate can rise.
              This means your mortgage payment would rise as well.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Low Down Payment</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              One of the biggest benefits of the FHA loan is its low down
              payment requirements. If you have a 580 credit score or higher,
              you can put down just 3.5 percent, compared to the typical 5
              percent you’ll need for conventional financing.
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              FHA loans also allow you to use qualified gift funds toward your
              down payment or closing costs. Talk with lenders about their
              policies and required documentation regarding gift funds.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">Many Home Types are Eligible</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              FHA loans can be used on a variety of property types, including
              single-family homes, condo units, duplexes, manufactured housing
              and mobile homes.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-5 center">
            <div className="card-start-tab">
              <p className="sub-text-1">Take your first step today</p>
              <a className="link" href="/start">
                Get a quote for a low money down FHA Home Loan →
              </a>
            </div>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <h3 className="head-text-1">First-Time Home buyers</h3>
            <hr className="loan-details-hr" />
            <p className="sub-text-1 sub-text-margin-bottom">
              The FHA loan is particularly popular with first-time home buyers,
              largely because of its low down payment requirements. Their more
              lenient credit standards are also helpful to first-timers, who
              many times are lacking a robust credit history.
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              Other loan types may prove more difficult for new home buyers,
              simply due to more stringent credit, income and down payment
              requirements. Many first-time buyers don’t have deep savings
              accounts or the ability to make a sizable down payment. FHA loans
              offer an affordable, hassle-free route toward home ownership for
              these cash-strapped buyers.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanBenefits;
