import React from "react";

import "./FhaLoanLimits.css";

function FhaLoanLimits() {
  return (
    <div
      id="fha-loan-limit"
      className="fha-loan-limit dark-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-7.png"
              alt="/images/loan-guide/s-7.png"
            />
            <p className="header-text-3">SECTION 7</p>
            <h4 className="header-text-1">FHA Loan Limits</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-2"></div>
          <div className="col-sm-6">
            <p className="sub-text-1 sub-text-margin-bottom">
              Limits on FHA loans vary by location and property type. They
              amount to 115 percent of the county’s median home price, so they
              fluctuate every year as the local market changes. You can find
              your FHA loan limits using this tool at HUD.gov.
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              FHA lenders also offers jumbo loans, meaning loans in excess of
              the FHA limit for your area. For jumbo FHA loans, lenders may have
              tighter guidelines for minimum credit score, credit history, DTI
              ratio and more.
            </p>
          </div>
          <div className="col-sm-1"></div>
          <div className="col-sm-3 center">
            <a href="/tools/fha-loan-limits" className="step-card-loan-guid">
              <div className="row">
                <div className="col-3 col-sm-12">
                  <img src="/images/tools/t-1.png" alt="img" />
                </div>
                <div className="col-9 col-sm-12">
                  <h5>Check Loan Limits In Your Area</h5>
                  <p>
                    Use our FHA Loan Limit Calculator to see what you may
                    qualify for.
                  </p>
                  <p>Check loan limit now!</p>
                </div>
              </div>
            </a>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanLimits;
