import React from "react";

import "./FhaLoanClosing.css";

function FhaLoanClosing() {
  return (
    <div
      id="fha-loan-closing-cost"
      className="fha-loan-closing-cost light-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-8.png"
              alt="/images/loan-guide/s-8.png"
            />
            <p className="header-text-3">SECTION 8</p>
            <h4 className="header-text-1">FHA Loan Closing Costs</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              All mortgage loans come with certain closing costs, which can vary
              from lender to lender. On FHA loans, closing costs tend to equal
              around 2 to 5 percent of the total loan amount. Just a few of the
              closing costs you may need to pay on your FHA loan include an
              origination fee, survey, appraisal, underwriting fee and more.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              As the buyer, you are responsible for covering closing costs, but
              with an FHA loan, you can ask the seller to contribute a portion
              of them. These are called concessions, and sellers can put up to 6
              percent of the home’s total sales price toward your closing costs.
              Sellers cannot contribute toward your down payment requirement.
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>Back to Top</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanClosing;
